import test from 'ava'
import window from 'window'
import {returnInfo} from '../bundle.js'
let windowObject = new window()

test('Checking returnInfo function', t => {
  const x = returnInfo('testing_return_info', windowObject.document)
  // console.log(windowObject.document.body.innerHTML)
  const expectedObj = {
    'name': 'testing_return_info',
    'name_length': ('testing_return_info').length,
    'content': windowObject.document.body.innerHTML
  }
  t.deepEqual(x, expectedObj)
})
